import React from "react";
import { Field, InjectedFormProps, reduxForm } from "redux-form";
import { GetStringKeys, Input, createField } from "../common/FormControls/FormControls.tsx";
import { required } from "../../utilities/validators/validators.ts";
import { connect, useDispatch, useSelector } from "react-redux";
import { login } from "../../redux/auth-reducer.ts";
import { Navigate } from "react-router-dom";
import styles from "./../common/FormControls/FormControls.module.css";
import { AppDispatch, AppStateType } from "../../redux/redux-store.ts";
import { getIsAuthSelector } from "../../redux/auth-selectors.ts";

type LoginFormOwnProps = {
    captchaUrl: string | null
}

//  1.  InjectedFormProps,   LoginFormValues  LongFormOwnProps  redux-form  2. LoginFormOwnProps (    -)
const LoginForm: React.FC<InjectedFormProps<LoginFormValuesType, LoginFormOwnProps> & LoginFormOwnProps> = ({handleSubmit, error, captchaUrl}) => { //   ( )
    return <form action="" onSubmit={handleSubmit}> 
        {/* <div>
            <Field component={Input} validate={[required]}
                name="email" type="text" placeholder="Email" />
        </div>
        <div>
            <Field component={Input} validate={[required]}
                name="password" type="password" placeholder="Password" />
        </div>
        <div>
            <Field component={Input} name="rememberMe" type="checkbox" />  
        </div>  */}
        {/*   createField    */}
        {createField<LoginFormValuesTypeKeys>("free@samuraijs.com", "email", [required], Input)}
        {createField<LoginFormValuesTypeKeys>("free", "password", [required], Input, { type: "password" })}
        <div className={styles.rememberMe}>
            <Field component={Input} name="rememberMe" type="checkbox" /> <div> </div>
        </div>

        {captchaUrl && <img alt="captcha" src={captchaUrl} />}
        {captchaUrl && createField('  ','captcha',[required],Input)}
        {error && <div className={styles.formSummaryError}>
            {/*     ,    _error */}
            {error}
        </div>}
        <div>
            <button></button>
        </div>
    </form>
}















const LoginReduxForm = reduxForm<LoginFormValuesType, LoginFormOwnProps>({ form: 'login' })(LoginForm)




export type LoginFormValuesType = {
    email: string
    password: string
    rememberMe: boolean
    captcha: string
}

// type LoginFormValuesTypeKeys = keyof LoginFormValuesType // ..   string  number
//type LoginFormValuesTypeKeys = Extract<keyof LoginFormValuesType, string> //    strings
type LoginFormValuesTypeKeys = GetStringKeys<LoginFormValuesType> // ,  ,  

export const LoginPage: React.FC = (props) => {

    const captchaUrl = useSelector((state: AppStateType) => state.auth.captchaUrl)
    const isAuth = useSelector(getIsAuthSelector)

    const dispatch: AppDispatch = useDispatch()

    const onSubmit = (formData: LoginFormValuesType) => {
        dispatch(login(formData.email, formData.password, formData.rememberMe, formData.captcha))
    }
    if (isAuth) {
        return <Navigate to={"/profile"} />
    }

    return <div>
        <h1></h1>
        <LoginReduxForm onSubmit={onSubmit} captchaUrl={captchaUrl} />
    </div>
}