import { InjectedFormProps, reduxForm } from 'redux-form'
import { GetStringKeys, Input, Textarea, createField } from '../../common/FormControls/FormControls.tsx'
import css from './ProfileInfo.module.css'
import formCss from "./../../common/FormControls/FormControls.module.css";
import React from 'react';
import { ProfileType } from '../../../types/types.ts';


type PropsType = {
    profile: ProfileType
    // handleSubmit: 
    // error:   
}
type ProfileTypeKeys = GetStringKeys<ProfileType>

const ProfileDataForm: React.FC<InjectedFormProps<ProfileType, PropsType> & PropsType> = ({ handleSubmit, profile, error }) => {
    return ( // ,    -    ,    onSubmit={onSubmit},   onSubmit={handleSubmit}
        <form onSubmit={handleSubmit} className={css.description__info}>
            <div>  {createField('', 'fullName', [], Input)}</div>
            <div> : {createField(' ', 'aboutMe', [], Textarea)}</div>
            <div> ?
                {createField<ProfileTypeKeys>('', 'lookingForAJob', [], Input, { type: "checkbox" })}
            </div>
            <div>
                {createField<ProfileTypeKeys>('  ', 'lookingForAJobDescription', [], Textarea)}
            </div>
            <div>
                <div><b></b></div>
                {Object.keys(profile.contacts).map(key => {
                    return <div key={key}>
                         {key}: {createField(key, 'contacts.' + key, [], Input)}
                        {/*  : -    */}
                    </div>
                })}
            </div>
            {error && <div className={formCss.formSummaryError}>
                {error}
            </div>}
            <div><button></button></div>
            {/* ..   ,       */}
        </form>
    )
}

const ProfileDataReduxForm = reduxForm<ProfileType, PropsType>({ form: 'edit-profile', destroyOnUnmount: false })(ProfileDataForm) //destroyOnUnmount:false    initialValues



export default ProfileDataReduxForm

