import css from './ProfileInfo.module.css'

import React, { ChangeEvent, useEffect, useState } from "react";
import { Button, Input } from 'antd'
const { TextArea } = Input;

type PropsType = {
    isOwner: boolean
    status: string
    updateStatus: (status: string) => void
}

const ProfileStatusWithHooks: React.FC<PropsType> = (props) => {

    let [editMode, setEditMode] = useState(false)
    let [status, setStatus] = useState(props.status) //     status      ,   ,       ,    updateStatus

    const activateEditMode = () => {
        if (props.isOwner) setEditMode(true)
    }
    const deactivateEditMode = () => {
        setEditMode(false)
        props.updateStatus(status) //     
    }
    const onStatusChange = (event: ChangeEvent<HTMLInputElement>) => {
        setStatus(event.currentTarget.value)
    }

    useEffect (() => { //  - ,  useEffect   .    
        setStatus(props.status)
    }, [props.status] ) //      ,  componentDidUpdate


    return (
        <div>
            {!editMode &&
                <div>
                    <span className={css.change__status} onClick={activateEditMode}>
                        {!props.status ? ' ' : props.status}
                    </span>
                </div>
            }
            {editMode &&
                <div>
                    <TextArea autoSize maxLength={300} placeholder="300  - " onChange={onStatusChange} autoFocus={true} onBlur={deactivateEditMode} value={status} />
                </div>
            }
        </div>
    )
}

export default ProfileStatusWithHooks;